import React, { Component } from 'react';


export class Ved extends Component {
    constructor(props){
        super(props);
        this.state={'posts':[]};
      }
      componentDidMount(){
        console.log('comp mounted');
      }
  
      componentDidUpdate(previousProps) {
        if (previousProps.si !== this.props.si) {
            this.getData();
        }
    }
 
      getData(){
       
        fetch(`https://api.agify.io?name=${this.props.si}`)
        .then(response => response.json())
        .then(json => this.setState({ posts: [json] }))
      }


    render() {
        const {posts} =this.state;
        console.log(this.props.si);
        return (
          <div className="container">
          <h2>name-age</h2>
           {posts.map((post) => (
            <p key={post.name}>the age is : {post.age}</p>
        ))}
                <h3>***********</h3>
          </div>
        )
    }
}

export default Ved
