import React, { Component } from 'react'
import Ved from './Ved';

export class Search extends Component {

    constructor(props){
        super(props);
        this.state={searchInput:"",submit:""};
        this.handleSubmit=this.handleSubmit.bind(this);
    }
    handleSubmit(event){
        event.preventDefault()
        this.setState({submit:this.state.searchInput})
    }

    handleChange(event){
        this.setState({searchInput:event.target.value});
    }

    render() {
        return (
            <div>
                <form onSubmit={this.handleSubmit}>
                    <input  value={this.state.searchInput} onChange={this.handleChange.bind(this)} /> 
                    <button type='submit'>enter</button>              
                </form>
                <Ved si={this.state.submit}/>
            </div>
        )
    }
}

export default Search
